import { Table } from "react-bootstrap";
import { memo, useState } from "react"
import { useSelector, useDispatch } from 'react-redux';
import { changeName, increase } from '../store/userSlice.js';
import { upCount, delCart } from '../store/store';



// memo) 감싸진 컴포넌트에서 기존 props와 비교해서 변했을때만 렌더링
// let Child = memo(function(){
//     console.log('재렌더링');
//     return <div>자식</div>
// })


// function 함수(){ 
//     return 반복문 10억번 돌린 결과;
// }


function Cart() {

    let state = useSelector((state) =>  state )
    let dispatch = useDispatch()  // store.js로 요청보내주는 함수
    let [count, setCount] = useState(0);

    // useMemo) 컴포넌트 렌더링 시 1회만 실행해줌. useEffect와 유사
    // useMemo는 렌더링 될 때, useEffect는 HTML코드 다 읽은 후 에 실행.
    // let result = useMemo(()=>{ return 함수()}, [])

    return (
        <div>
            {/* <Child></Child>
            <button onClick={()=>{ setCount(count++) }}> + </button> */}
            <p>{state.user.name}{state.user.age}의 장바구니</p>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>상품명</th>
                        <th>수량</th>
                        <th>변경하기</th>
                        <th>삭제</th>
                    </tr>
                </thead>
                <tbody>
                    { 
                        state.cart.map((a, i) =>
                            <tr key={i}>
                                <td>{i+1}</td>
                                <td>{a.name}</td>
                                <td>{a.count}</td>
                                <td>
                                    <button onClick={()=>{
                                        dispatch(upCount(a.id));
                                    }}> + </button>
                                </td>
                                <td>
                                    <button onClick={()=>{
                                        dispatch(delCart(a.id));
                                    }}> - </button>
                                </td>
                            </tr>
                        )
                    }
                </tbody>
            </Table>
        </div>
    )
}


export default Cart;